@import '../../00-base/00-defaults/01-variables';
.menu-bar {
  ul.inline-menu {
    li.inline-menu__item {
      margin: 0!important;
      a.inline-menu__link {
        font-size: 1.1rem;
        font-weight: 400;
        line-height: 1.5;
        color: $defaultcolor;
        text-transform: capitalize;
        padding: 0 0.3rem !important;
        margin: 0.667rem 1rem !important;
        &:hover {
          color: $redcolor;
        }
      }
    }
  }
}
